#localization {
    width: 100vw;
    max-width: 100%;
}

.localization {
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.localization .left,
.localization .right {
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    margin-bottom: 150px;
}

.localization .left .localization-title,
.localization .right .localization-title {
    font-size: 4vw;
    color: var(--main-gold-color);
    width: 100%;
    max-width: 100%;
    text-align: center;
    margin-bottom: 30px;
}

.localization .left .localization-info,
.localization .right .localization-info {
    font-family: 'Muli', sans-serif;
    font-size: 1.6vw;
    width: 100%;
    text-align: center;
}

.localization .left .localization-info p,
.localization .right .localization-info p {
    margin: 10px;
}

.localization .left {
    border-right: 1.2px solid black;
}

.localization .right {
    border-left: 1.2px solid black;
}

.map-container {
    width: 100%;
    max-width: 100%;
    height: 600px;
}

.marker {
    margin-bottom: 30px;
    width: 5vw;
    height: auto;
}