.menu {
    width: 100vw;
    max-width: 100%;
    height: 60px;
    position: fixed;
    left: 0;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.top-bar {
    width: 70%;
    height: 100%;
    display: flex;
    align-items: center;
    z-index: 9999;
}

.menu-background {
    width: 100vw;
    max-width: 100%;
    height: 60px;
    position: fixed;
    -webkit-transition: all ease-in 0.5s;
    transition: all ease-in 0.5s;
    opacity: 0;
}

.menu-background.sticked {
    background: black;
    opacity: 0.6;
    -webkit-transition: all ease-in 0.5s;
    transition: all ease-in 0.5s;
    z-index: 1;
}
 
.menu .logo {
