    width: 100%;
    height: 100%;
}

.menu-container ul {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    list-style-type: none;
}

.menu-container li{
    height: 100%;
}

.menu-container a {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-bottom: transparent 3px solid;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Muli', sans-serif;

}

.menu-container ul a.current {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-bottom: var(--main-gold-color) 3px solid;
    color: var(--main-gold-color);
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transition: 0.25s;
    transition: 0.25s;
  }

.menu-item {
    font-size: 22px;
    margin-left: 25px;
    color: white;
    text-decoration: none;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transition: 0.25s;
    transition: 0.25s;
}

.menu-item:hover {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-bottom: var(--main-gold-color) 3px solid;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transition: 0.25s;
    transition: 0.25s;
}
