html {
    scroll-behavior: smooth;
    font-family: 'Dancing Script';
}

body {
    margin: 0;
    padding: 0;
    text-decoration: none;
}

::-webkit-scrollbar {
    width: 7px;
  }
  
/* Track */
::-webkit-scrollbar-track {
    background: rgb(44, 44, 44); 
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #d4af37;
    background: var(--main-gold-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: white;
} 

.react-parallax {
    max-width: 100%;
}

mark.gold {
    color: #d4af37;
    color: var(--main-gold-color);
    background: none;
}

::-moz-selection {
    background: #d4af37;
    background: var(--main-gold-color);
}

::selection {
    background: #d4af37;
    background: var(--main-gold-color);
}

::-moz-selection {
    background: #d4af37;
    background: var(--main-gold-color);
}
